import { Box, BoxProps, Button, useDisclosure } from '@chakra-ui/react'
import { MutableRefObject, useRef } from 'react'
import { CardDrawer } from './CardDrawer'

export type CardDrawerBoxProp = {} & BoxProps
export const CardDrawerBox = (props: CardDrawerBoxProp) => {
  const { ...boxProps } = props
  const { isOpen, onOpen, onClose } = useDisclosure()
  const btnRef = useRef() as MutableRefObject<HTMLButtonElement>
  return (
    <Box {...boxProps}>
      <Button ref={btnRef} colorScheme='teal' onClick={onOpen}>
        Cards
      </Button>
      <CardDrawer
        btnRef={btnRef}
        isOpen={isOpen}
        onClose={onClose}
      ></CardDrawer>
    </Box>
  )
}
