import {
  Text,
  Box,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Center,
  HStack,
  Button,
} from '@chakra-ui/react'
import {
  LocationData,
  LocationDataDiff,
  LocationDataDiffs,
  useGameStore,
} from '../stores/useGameStore'
import { allCards } from '../game-data/cards'
import { useEffect, useState } from 'react'
import { produce } from 'immer'
import { CardId } from '../game-data/types'
import { useScenesStore } from '../stores/useScenesStore'
import { useStateManager } from '../hooks/useStateManager'
const size = 18

export const Map = () => {
  const { locations } = useGameStore()
  const { sendLocal } = useStateManager()
  const { scModifyClansControl, scModifyClansData } = useScenesStore()
  const { rendered: controlRendered, data } = scModifyClansControl
  const { rendered: dataRendered, data: dataData } = scModifyClansData
  const [diff, setDiff] = useState<LocationDataDiffs>({})

  useEffect(() => {
    if (dataData) {
      // console.log('diff updated by data:', controlData)
      setDiff(dataData)
    }
  }, [dataData])

  // console.log('scModifyClans', scModifyClansControl, diff)

  const [currentLocation, setCurrentLocation] = useState<CardId>()

  const handleConfirm = () => {
    sendLocal('evLocationUpdated', diff)
  }
  const handleModifierChange: React.MouseEventHandler<HTMLButtonElement> = (
    evt
  ) => {
    if (!currentLocation) return
    if (!diff[currentLocation]) {
      produce(diff, (draft) => {
        draft[currentLocation] = {
          cardId: currentLocation,
          clans1: 0,
          clans2: 0,
          sanctuaries: 0,
          citadels: 0,
          festival: false,
          capital: locations[currentLocation]?.capital ? true : false,
        }
      })
    }
    const { value } = evt.target as HTMLButtonElement
    switch (value) {
      case 'clan1p':
        setDiff(
          produce((draft) => {
            draft[currentLocation]!.clans1 += 1
          })
        )
        break
      case 'clan1m':
        setDiff(
          produce((draft) => {
            draft[currentLocation]!.clans1 -= 1
          })
        )
        break
      case 'clan2p':
        setDiff(
          produce((draft) => {
            draft[currentLocation]!.clans2 += 1
          })
        )
        break
      case 'clan2m':
        setDiff(
          produce((draft) => {
            draft[currentLocation]!.clans2 -= 1
          })
        )
        break
      case 'sancp':
        setDiff(
          produce((draft) => {
            draft[currentLocation]!.sanctuaries += 1
          })
        )
        break
      case 'sancm':
        setDiff(
          produce((draft) => {
            draft[currentLocation]!.sanctuaries -= 1
          })
        )
        break
      case 'citdp':
        setDiff(
          produce((draft) => {
            draft[currentLocation]!.citadels += 1
          })
        )
        break
      case 'citdm':
        setDiff(
          produce((draft) => {
            draft[currentLocation]!.citadels -= 1
          })
        )
        break
      case 'festival':
        setDiff(
          produce((draft) => {
            draft[currentLocation]!.festival = !draft[currentLocation]!.festival
          })
        )
        break
      default:
        break
    }
  }
  const handleCardClick = (id: CardId) => {
    if (!diff[id]) {
      setDiff(
        produce((draft) => {
          draft[id] = {
            cardId: id,
            clans1: 0,
            clans2: 0,
            sanctuaries: 0,
            citadels: 0,
            festival: false,
            capital: locations[id]?.capital ? true : false,
          }
        })
      )
    }
    setCurrentLocation(id)
  }
  return (
    <Center id='map' w='100dvw' h='90dvh' overflow='scroll'>
      {controlRendered ? (
        <Box position='absolute' top={10} left={10}>
          <Button
            className='player1'
            value='clan1p'
            onClick={handleModifierChange}
          >
            {' + '}
          </Button>
          <Button
            className='player1'
            value='clan1m'
            onClick={handleModifierChange}
          >
            {' - '}
          </Button>
          <Button
            className='player2'
            value='clan2p'
            onClick={handleModifierChange}
          >
            {' + '}
          </Button>
          <Button
            className='player2'
            value='clan2m'
            onClick={handleModifierChange}
          >
            {' - '}
          </Button>
          <Button value='sancp' onClick={handleModifierChange}>
            {' + '}
          </Button>
          <Button value='sancm' onClick={handleModifierChange}>
            {' - '}
          </Button>
          <Button
            position='absolute'
            top={10}
            left={10}
            onClick={handleConfirm}
          >
            Confirm Change
          </Button>
        </Box>
      ) : undefined}
      <Box overflow='scroll'>
        <HStack gap='0'>
          {Object.keys(locations).map((cardIdStr) => (
            <Location
              key={cardIdStr}
              locationData={locations[cardIdStr as CardId]!}
              onClick={handleCardClick}
              isModifyClans={dataRendered}
              isActive={currentLocation === cardIdStr}
              locationDiff={diff[cardIdStr as CardId] ?? undefined}
            />
          ))}
        </HStack>
        <HStack gap='0' paddingLeft={`${size / 2}dvw`}></HStack>
      </Box>
    </Center>
  )
}

type LocationProps = {
  locationData: LocationData
  onClick: (id: CardId) => void
  isActive: boolean
  isModifyClans: boolean
  locationDiff: LocationDataDiff | undefined
}

export const Location = (props: LocationProps) => {
  const {
    locationData: info,
    onClick,
    isModifyClans,
    isActive,
    locationDiff: diff,
  } = props

  const clanStyle = (player1: boolean) => {
    return {
      fontSize: '5dvh',
      color: player1 ? 'red' : 'blue',
    }
  }
  const margin = '0.5dvh'
  // console.log('info:', info, 'diff:', diff)
  const handleClick = () => {
    onClick(info.cardId)
  }
  // console.log('Render:', info)
  return (
    <Card
      w={`${size}dvw`}
      h={`${size}dvh`}
      bg={isActive ? 'yellow' : 'green'}
      overflow='hidden'
      onClick={handleClick}
    >
      <CardHeader h='4dvh' p='5px'>
        <Text as='b' fontSize='xl'>
          {allCards[info.cardId as CardId].title} {info.capital ? '(c)' : ''}
        </Text>
      </CardHeader>
      <CardBody
        h='7vh'
        p='0px'
        marginRight={margin}
        marginLeft={margin}
        bg='#FFFFFF'
        overflow='hidden'
      >
        <span>
          <span style={clanStyle(true)}>
            {info.clans1} {isModifyClans ? `(${diff?.clans1})` : ''}
          </span>
          <span style={clanStyle(false)}>
            {info.clans2} {isModifyClans ? `(${diff?.clans2})` : ''}
          </span>
        </span>
      </CardBody>
      <CardFooter
        p='6px'
        h='7dvh'
        marginRight='5px'
        marginLeft='5px'
        marginBottom={margin}
        bg='#888888'
      >
         {info.citadels} {isModifyClans ? `(${diff?.citadels})` : ''}/ {' '}
        {info.sanctuaries} {isModifyClans ? `(${diff?.sanctuaries})` : ''}
      </CardFooter>
    </Card>
  )
}
