import { usePhaseSetup } from './game-phases/usePhaseSetup'
import { usePhaseDraftCards } from './game-phases/usePhaseDraftCards'
import { usePhaseSeason } from './game-phases/usePhaseSeason'
import { PlayerId } from '../stores/useGameStore'
import { Network } from '../utils/dummy-network'

export type Phase = 'pSetup' | 'pDraftCards' | 'pSeason'
export type GameEvent =
  | 'evTurnSwitched'
  | 'evGoHandle'
  | 'evPhaseStarted'
  | 'evStart'
  | 'evPlayer1UrlGenerated'
  | 'evPlayer2PeerParamGenerated'
  | 'evPeerParamsUpdatedForPlayer1'
  | 'evPeerConnected'
  | 'evLocationInstalled'
  | 'evLocationUpdated'
  | 'evDraftCardsSelected'
  | 'evCardSelected'
  | 'evActionPlanned'
  | 'evConfirmed'

export type State =
  | 'stInit' // pSetup
  | 'stStartSelectingPlayer'
  | 'stFinishingSelectingPlayer'
  | 'stLocationInstalling'
  | 'stSettingUpInitialClans'
  | 'stSettingUpInitialClansWait'
  | 'stSettingUpInitialClansDone'
  | 'stDraftCardsSetup' //pDraftsCards
  | 'stDistributeCards'
  | 'stDraftCardsDone'
  | 'stStartTurn' // pSeason
  | 'stStartStep'
  | 'stSelectingCard'
  | 'stWaitWithInfo'
  | 'stPlanningAction'
  | 'stEndStep'
  | 'stEndTurn'

// event ---> State.handle(event) ---> Phase.goNext(handleResult) ---> setState(st, value) ---> State.initialize(initData)

export type StateManagerInterface = {
  updatePhase: (newPhase: Phase, value: unknown) => void
  updateState: (newState: State, value: unknown) => void
  getState: () => State
  getPhase: () => Phase
  sendLocal: (event: GameEvent, value: unknown) => void
  sendRemote: (event: GameEvent, value: unknown) => void
  received: (event: GameEvent, value: unknown) => void
  setupNetwork: (player: PlayerId, peerParams: string) => void
  updatePeerParams: (param: string) => void
}

export var phase: Phase = 'pSetup'
export var state: State = 'stInit'
export var phasesData: { [phase in Phase]: PhaseData }
export var statesData: { [st in State]: StateData }
export var stateValue: any = {}
export var dev: any = false

export const stateLog = (...args: any[]) => {
  console.log(`[${phase}]{${state}}\n`, ...args)
}

// export const stateLog = console.log.bind(
//   console.log,
//   `[${phase}]{${state}}/`,
//   stateValue,
//   '\n'
// )

export type StateData = {
  id: State
  validEvents: GameEvent[]
  initialize?: (value: unknown) => void
  handle: (event: GameEvent, value: any) => any
}

export type PhaseData = {
  id: Phase
  goNext: (value: unknown) => void
  states: State[]
}

const getPhase = () => phase
const getState = () => state

// message sender
let received = (event: GameEvent, value: unknown): void => {
  setTimeout(() => {
    stateLog(
      `<-(remote)- (${event})`,
      value,
      phasesData[phase].states,
      `{${state}}`
    )
    sendLocal(event, value)
  })
}

let sendRemote = (event: GameEvent, value: unknown): void => {
  throw new Error('not initiated')
}

let updatePeerParams = (param: string): void => {
  throw new Error('not initiated')
}

const setupNetwork = (player: PlayerId, peerParams: string) => {
  const { send, updatePeerParams: updatePeerParamsNetwork } = Network(
    player,
    peerParams ?? window.location.search,
    received
  )

  sendRemote = send
  updatePeerParams = updatePeerParamsNetwork
  // received = receivedFromRemote
}

const sendLocal = (event: GameEvent, value: unknown) => {
  setTimeout(() => {
    const index = phasesData[phase].states.indexOf(state)
    stateLog(`<-- (${event})`, value)
    if (index > -1) {
      if (
        statesData[phasesData[phase].states[index]].validEvents.includes(event)
      ) {
        phasesData[phase].goNext(
          statesData[phasesData[phase].states[index]].handle(event, value)
        )
      } else {
        console.warn(`invalid (${event}) in {${state}}[${phase}]`)
      }
    } else {
      console.warn(`State in not valid (${event}) in {${state}}[${phase}]`)
    }
  })
}

export const useStateManager = () => {
  const updatePhase = (newPhase: Phase, value: unknown) => {
    stateLog('newPhase', newPhase, value)
    phase = newPhase
    updateState(phasesData[phase].states[0], value)
  }
  const updateState = (newState: State, value: unknown) => {
    stateLog('newState', newState)
    state = newState
    setTimeout(() => statesData[newState]?.initialize?.(value))
  }
  const phaseProperties = {
    updatePhase,
    updateState,
    getPhase,
    getState,
    sendLocal,
    sendRemote,
    received,
    setupNetwork,
    updatePeerParams,
  }
  const { phase: setupPhase, states: setupStates } =
    usePhaseSetup(phaseProperties)
  const { phase: cardDraftPhase, states: cardDraftStates } =
    usePhaseDraftCards(phaseProperties)
  const { phase: seasonPhase, states: seasonStates } =
    usePhaseSeason(phaseProperties)

  phasesData = {
    pSetup: setupPhase,
    pDraftCards: cardDraftPhase,
    pSeason: seasonPhase,
  }

  statesData = {
    ...setupStates,
    ...cardDraftStates,
    ...seasonStates,
  }
  return { sendLocal, received, sendRemote }
}
