import { create } from 'zustand'
import { devtools } from 'zustand/middleware'
import { immer } from 'zustand/middleware/immer'

export type Scene =
  | 'scInitial'
  | 'scSelectPlayer'
  | 'scInstallLocation'
  | 'scModifyClansControl'
  | 'scModifyClansData'
  | 'scWaitForOpponent'
  | 'scDraftCards'
  | 'scDraftCardsWait'
  | 'scCardSelecting'
  | 'scTurnInfo'
  | 'scShowConfirm'

export type SceneData = {
  rendered: boolean
  data: any
}
type SceneStore = {
  [scene in Scene]: SceneData
}
type SceneStoreSetter = {
  setScene: (sc: Scene, rendered: boolean, data: any) => void
  setScenes: (scs: { sc: Scene; rendered: boolean; data: any }[]) => void
}

export const useScenesStore = create<SceneStore & SceneStoreSetter>()(
  devtools(
    immer((set, get) => ({
      scInitial: { rendered: true, data: null },
      scSelectPlayer: { rendered: false, data: null },
      scInstallLocation: { rendered: false, data: null },
      scModifyClansControl: { rendered: false, data: null },
      scModifyClansData: { rendered: false, data: null },
      scWaitForOpponent: { rendered: false, data: null },
      scDraftCards: { rendered: false, data: null },
      scDraftCardsWait: { rendered: false, data: null },
      scCardSelecting: { rendered: false, data: null },
      scTurnInfo: { rendered: true, data: null },
      scShowConfirm: { rendered: true, data: null },

      setScene: (sc: Scene, rendered: boolean, data: any) =>
        set((state) => {
          state[sc] = { rendered: rendered, data: data }
        }),
      setScenes: (scs: { sc: Scene; rendered: boolean; data: any }[]) =>
        set((state) => {
          scs.forEach(
            (item) =>
              (state[item.sc] = { rendered: item.rendered, data: item.data })
          )
        }),
    }))
  )
)
