import { Button, Input, Radio, RadioGroup, Stack } from '@chakra-ui/react'
import { useStateManager } from '../hooks/useStateManager'
import { MutableRefObject, useCallback, useRef, useState } from 'react'

export const ChoosePlayer = (props: {
  player1Url: string
  player2PeerParam: string
}) => {
  const [player, setPlayer] = useState('1')
  const { sendLocal } = useStateManager()
  const inputRef = useRef(null) as MutableRefObject<HTMLInputElement | null>
  const { player1Url, player2PeerParam } = props

  const handleClick = useCallback(() => {
    sendLocal('evPlayer1UrlGenerated', {
      playerId: parseInt(player),
      peerParams: '',
    })
  }, [player])
  const setValue = (val: string) => {
    setPlayer(val)
  }

  const handleCopyClick = () => {
    const copiedText = inputRef.current?.placeholder
    console.log('copied:', copiedText)
    if (copiedText) navigator.clipboard.writeText(copiedText)
  }

  const handleApplyClick = useCallback(() => {
    const copiedText = inputRef.current?.value
    console.log('applying:', copiedText)
    sendLocal('evPeerParamsUpdatedForPlayer1', copiedText)
  }, [player])
  const inputValue = player1Url ?? player2PeerParam ?? ''

  return (
    <>
      <RadioGroup onChange={setValue} value={player}>
        <Stack direction='row'>
          <Radio value='1'>Player 1</Radio>
          <Radio value='2'>Player 2</Radio>
        </Stack>
      </RadioGroup>

      <Input ref={inputRef} placeholder={inputValue}></Input>
      <Button onClick={handleCopyClick}>Copy</Button>
      <Button onClick={handleApplyClick}>Apply</Button>
      <Button onClick={handleClick}>Choose Player</Button>
    </>
  )
}
