export const actionCardList: CardId[] = Object.keys(allCards).filter(
  (key) => allCards[key as CardId].type === 'action'
) as CardId[]

export const cardPiles = (() => {
  let setAsided: CardId | undefined
  let actionDisposed: CardId[] = []
  let epicDisposed: CardId[] = []
  let landDisposed: CardId[] = []
  let actionDeck: CardId[] = []
  let epicDeck: CardId[] = []
  let landDeck: CardId[] = []

  Object.keys(allCards).forEach((key) => {
    const item = allCards[key as CardId]
    switch (item.type) {
      case 'action':
        actionDeck.push(item.id)
        break
      case 'epic':
        epicDeck.push(item.id)
        break
      case 'land':
        landDeck.push(item.id)
        break
    }
  })
  return {
    setAsided,
    actionDisposed,
    actionDeck,
    epicDeck,
    epicDisposed,
    landDeck,
    landDisposed,
  }
})()
