import { VStack } from '@chakra-ui/react'
import { allCards } from '../../game-data/cards'
import { globals } from '../../game-data/globals'
import { CardActionKind, CardId, CardPlayType } from '../../game-data/types'
import {
  LocationDataDiff,
  LocationDataDiffs,
  PlayerId,
  useGameStore,
} from '../../stores/useGameStore'
import { useScenesStore } from '../../stores/useScenesStore'
import {
  GameEvent,
  PhaseData,
  State,
  StateData,
  stateLog,
  StateManagerInterface,
} from '../useStateManager'
import {
  copyLocationDataDiffs,
  discardActionCard,
  mergeLocationDiff,
} from '../../utils/utils'
import { useInfoStore } from '../../stores/useInfoStore'

export type CardSelectingValue = {
  cardPlayType: CardPlayType
  showConfirm: boolean
  hasGeis: boolean
}
export type CardSelectingResult = {
  player: PlayerId
  cardId: CardId | undefined
}
export type WaitForConfirmValue = {
  needConfirm: boolean
}
export type WaitForConfirmResult = {
  triskelPlayed: boolean
}

type StatesPhase = Extract<
  State,
  | 'stStartTurn'
  | 'stStartStep'
  | 'stSelectingCard'
  | 'stWaitWithInfo'
  | 'stPlanningAction'
  | 'stEndStep'
  | 'stEndTurn'
>

var turn = 1

// 1: opponent skipps triskel card against season card after p1 selects
// 2: opponent skipps triskel card against season card before p1 selects
var debug1 = true
var debug2 = false

// 3: opponent plays season card
var debug3 = 1

type StepData = {
  cardId?: CardId
  movingResult?: LocationDataDiffs
  clashResult?: LocationDataDiffs
  explorationResult?: LocationDataDiffs
  player: PlayerId
  cardPlayType: CardPlayType
}

type TurnData = {
  activePlayer: PlayerId
  stepSeq: StepData[]
}

var turnHistory: TurnData[] = []

var cardSeqInTurn: { player: PlayerId; cardId: CardId }[] = []

var currentStep: StepData = {
  player: globals.currentPlayer,
  cardPlayType: 'season',
}

var currentTurn: TurnData = {
  activePlayer: globals.currentPlayer,
  stepSeq: [],
}

type PhaseSeasonData = {
  turnSeq: TurnData[]
  stateValue: unknown
}

var phaseSeasonData: PhaseSeasonData = {
  turnSeq: [],
  stateValue: undefined,
}

var modifyClansResult: LocationDataDiffs = {}
var locationDiffInTurn: LocationDataDiffs = {}

var triskelInStep = new Array<CardId | undefined | null>(3)
triskelInStep = [null, null, null] // index 0 is unusable

export const usePhaseSeason = (phaseAndState: StateManagerInterface) => {
  const { updateState, getState, sendLocal, received } = phaseAndState
  const { setScene } = useScenesStore()
  const { setActiveInfo: setActiveInfoInternal } = useInfoStore()
  const {
    activePlayerOfTurn,
    setActivePlayerOfTurn,
    playerData,
    setPlayerData,
    updateLocation,
  } = useGameStore()

  const setActiveInfo = (desc: string) => {
    setActiveInfoInternal({
      desc,
      player: activePlayerOfTurn,
      phase: 'pSeason',
      state: getState(),
    })
  }

  const phase: PhaseData = {
    id: 'pSeason',
    states: [
      'stStartTurn',
      'stStartStep',
      'stSelectingCard',
      'stWaitWithInfo',
      'stPlanningAction',
      'stEndStep',
      'stEndTurn',
    ],
    goNext: function (value: unknown): void {
      const from = getState()
      switch (from) {
        case 'stStartTurn':
          updateState('stStartStep', value)
          break
        case 'stStartStep':
          updateState('stSelectingCard', null)
          break
        case 'stSelectingCard':
          updateState(value as State, null)
          break
        case 'stPlanningAction':
          updateState('stEndStep', value)
          break
        case 'stEndStep':
          if (value === 'triskel') {
            updateState('stStartStep', value)
          } else {
            updateState('stEndTurn', null)
          }
          break
        case 'stEndTurn':
          updateState('stStartTurn', value)
          break
        default:
          break
      }
    },
  }
  const states: { [id in StatesPhase]: StateData } = {
    stStartTurn: {
      id: 'stStartTurn',
      validEvents: ['evGoHandle'],
      initialize: (value: unknown) => {
        let player = activePlayerOfTurn
        stateLog('value:', value)
        if (value !== null) player = value as PlayerId
        locationDiffInTurn = {}
        cardSeqInTurn = []
        currentTurn = {
          activePlayer: player,
          stepSeq: [],
        }
        stateLog(currentTurn)
        setScene('scTurnInfo', true, { cards: [...cardSeqInTurn] })
        sendLocal('evGoHandle', 'season')
      },
      handle: function (event: GameEvent, value: any) {
        return value
      },
    },
    stStartStep: {
      id: 'stStartStep',
      validEvents: ['evGoHandle'],
      initialize: (value: unknown) => {
        stateLog('cardType:', value)
        currentStep = {
          player: currentTurn.activePlayer,
          cardPlayType: value as CardPlayType,
        }
        sendLocal('evGoHandle', null)
      },
      handle: function (event: GameEvent, value: any) {},
    },
    stSelectingCard: {
      id: 'stSelectingCard',
      validEvents: ['evCardSelected'],
      initialize: () => {
        stateLog(currentStep)
        setScene('scCardSelecting', true, {
          cardPlayType: currentStep.cardPlayType,
          showConfirm:
            (activePlayerOfTurn === globals.currentPlayer &&
              (currentStep.cardPlayType === 'season' ||
                triskelInStep[globals.currentPlayer] === null)) ||
            (activePlayerOfTurn !== globals.currentPlayer &&
              currentStep.cardPlayType !== 'season' &&
              triskelInStep[globals.currentPlayer] === null),
        } as CardSelectingValue)
        let msg = ''
        if (
          currentStep.cardPlayType === 'season' &&
          activePlayerOfTurn !== globals.currentPlayer
        ) {
          msg = "Wait for opponent's playing season card"
          if (debug3 === 1) {
            received('evCardSelected', {
              player: 2,
              cardId: playerData[2].cardIds[0],
            })
            debug3 = 2
          }
        } else if (currentStep.cardPlayType === 'season') {
          msg = 'Play season card'
        } else if (triskelInStep[globals.currentPlayer] === null) {
          msg = 'Play triskel card'
          if (debug2) {
            received('evCardSelected', { player: 2, cardId: undefined })
            debug2 = false
          }
        } else if (currentStep.cardPlayType === 'triskel') {
          msg = "Wait for opponent's playing triskel card"
          if (debug1) {
            received('evCardSelected', { player: 2, cardId: undefined })
            // debug1 = false
          }
        }
        setActiveInfo(msg)
      },
      handle: (event: GameEvent, value: any) => {
        stateLog(
          value,
          'currentStep:',
          currentStep,
          'triskelTmp:',
          triskelInStep
        )
        if (currentStep.cardPlayType === 'season') {
          currentStep.cardId = (value as CardSelectingResult).cardId
          cardSeqInTurn.push({
            player: activePlayerOfTurn,
            cardId: currentStep.cardId!,
          })
          setScene('scTurnInfo', true, { cards: [...cardSeqInTurn] })
          setScene('scCardSelecting', true, {
            cardPlayType: currentStep.cardPlayType,
            showConfirm: false,
          })
          return 'stPlanningAction'
        }
        // triskel card
        const v = value as CardSelectingResult
        triskelInStep[v.player] = v.cardId
        if (triskelInStep[1] === null || triskelInStep[2] === null) {
          return 'stSelectingCard'
        } else if (
          triskelInStep[1] === undefined &&
          triskelInStep[2] === undefined
        ) {
          return 'stEndStep'
        } else {
          let tmp =
            triskelInStep[1] !== undefined ? triskelInStep[1] : triskelInStep[2]
          currentStep.cardId = tmp
          cardSeqInTurn.push({
            player: v.player,
            cardId: currentStep.cardId!,
          })
          setScene('scTurnInfo', true, { cards: [...cardSeqInTurn] })
          return 'stPlanningAction'
        }
      },
    },
    stWaitWithInfo: {
      id: 'stWaitWithInfo',
      validEvents: [],
      initialize: undefined,
      handle: function (event: GameEvent, value: any) {
        throw new Error('Function not implemented.')
      },
    },
    stPlanningAction: {
      id: 'stPlanningAction',
      validEvents: ['evLocationUpdated'],
      initialize: (value: unknown) => {
        const cardId = currentStep.cardId
        if (activePlayerOfTurn === globals.currentPlayer)
          setActiveInfo('Plan action following the card played')
        else
          setActiveInfo(
            'Wait for oppoenent to Plan action following the card played'
          )
        switch (allCards[cardId!].actionSceneKind) {
          // case 'modifyClans':
          //   setScene('scModifyClansControl', true, null)
          //   setScene('scModifyClansData', true, locationDataDiff)
          //   // setScene('scWaitForOpponent', true, locationDataDiff)
          //   break
          default:
            setScene(
              'scModifyClansControl',
              activePlayerOfTurn === globals.currentPlayer,
              null
            )
            setScene('scModifyClansData', true, locationDiffInTurn)
            break
        }

        if (debug3 === 2) {
          received('evLocationUpdated', {
            lForest: {
              cardId: 'lForest',
              clans1: 0,
              clans2: 5,
              sanctuaries: 0,
              citadels: 0,
              festival: false,
              capital: false,
            },
          })
          debug3 = 3
        }
      },
      handle: function (event: GameEvent, value: any) {
        if (currentStep.cardId) {
          switch (allCards[currentStep.cardId].actionSceneKind) {
            default:
              stateLog(
                'planningAction:',
                JSON.stringify(currentStep.movingResult),
                JSON.stringify(value)
              )
              if (value) {
                currentStep.movingResult =
                  copyLocationDataDiffs(value as Partial<LocationDataDiffs>) ??
                  {}
                locationDiffInTurn =
                  copyLocationDataDiffs(value as Partial<LocationDataDiffs>) ??
                  {}
              }
              break
          }
        } else {
          console.log('NO Valid Card Id')
        }
        setScene('scModifyClansControl', false, null)
        setScene(
          'scModifyClansData',
          true,
          copyLocationDataDiffs(locationDiffInTurn)
        )
        return value
      },
    },
    stEndStep: {
      id: 'stEndStep',
      validEvents: ['evGoHandle'],
      initialize: () => {
        currentTurn.stepSeq.push({
          ...currentStep,
          movingResult: currentStep.movingResult
            ? { ...copyLocationDataDiffs(currentStep.movingResult) }
            : undefined,
          clashResult: currentStep.clashResult
            ? { ...copyLocationDataDiffs(currentStep.clashResult) }
            : undefined,
          explorationResult: currentStep.explorationResult
            ? { ...copyLocationDataDiffs(currentStep.explorationResult) }
            : undefined,
        })
        sendLocal('evGoHandle', null)
      },
      handle: function (event: GameEvent, value: any) {
        let tmp = [...playerData[currentStep.player].cardIds]
        tmp.splice(
          playerData[currentStep.player].cardIds.indexOf(currentStep.cardId!),
          1
        )
        setPlayerData(currentStep.player, { cardIds: tmp })
        discardActionCard(currentStep.cardId!)

        console.error('stEndStep', triskelInStep)
        if (triskelInStep[1] === undefined && triskelInStep[2] === undefined) {
          triskelInStep[1] = null
          triskelInStep[2] = null
          console.log('move to stEndTurn')
          return ''
        }
        triskelInStep[1] = null
        triskelInStep[2] = null

        return 'triskel'
      },
    },
    stEndTurn: {
      id: 'stEndTurn',
      validEvents: ['evGoHandle'],
      initialize: () => {
        stateLog(
          'endTurn:',
          JSON.stringify(currentStep),
          JSON.stringify(currentTurn)
        )
        Object.entries(locationDiffInTurn).forEach(([k, v]) => {
          console.log('updateLocation', k, v)
          updateLocation(v)
        })
        sendLocal('evGoHandle', null)
      },
      handle: function (event: GameEvent, value: any) {
        setScene('scModifyClansData', false, {})
        // switch active player of turn
        const newActivePlayer = currentTurn.activePlayer === 1 ? 2 : 1
        setActivePlayerOfTurn(newActivePlayer)
        return newActivePlayer
      },
    },
  }
  return { phase, states }
}

/*

  1. (o) stSelectingCard/season             (x)
  2. (o) stPlanningAction                   (x) <- Geis Can be Played!!!
  3. (x)    stConfirm                       (o) <-
  4. (o) ifClash -> stClash                 (o) <-
  4. (o) seSelectingCard/triskel or skip    (o)
  4.    (check who can play) -> go to 2
  5.  ( no one choose triskel) -> go to 1

  1.season -> 1.planningAction
  */
