import { actionCardList, cardPiles } from '../game-data/cards'
import { CardId } from '../game-data/types'
import {
  emptyLocationDataDiff,
  LocationData,
  LocationDataDiff,
  LocationDataDiffs,
} from '../stores/useGameStore'

export const drawLocationCard = () => {
  return cardPiles.landDeck.splice(
    Math.floor(Math.random() * cardPiles.landDeck.length),
    1
  )[0]
}

export const drawActionCard = () => {
  return cardPiles.actionDeck.splice(
    Math.floor(Math.random() * cardPiles.actionDeck.length),
    1
  )[0]
}

export const discardActionCard = (cardId: CardId) => {
  cardPiles.actionDisposed.push(cardId)
}

export const drawEpicCard = () => {
  return cardPiles.epicDeck.splice(
    Math.floor(Math.random() * cardPiles.epicDeck.length),
    1
  )[0]
}

export const refillActionCards = () => {
  cardPiles.actionDeck = [...actionCardList]
  cardPiles.actionDisposed = []
  cardPiles.setAsided = undefined
}

export const mergeLocationDiff = (
  state: LocationDataDiffs,
  diff: LocationDataDiffs
) => {
  // let stateValue: LocationDataDiffs = {...state }
  let stateValue: LocationDataDiffs = {}
  Object.entries(state).forEach(([k, v]) => {
    stateValue[k as CardId] = { ...v }
  })
  console.log('merge:', JSON.stringify(stateValue), JSON.stringify(diff))
  if (!diff) return stateValue
  Object.entries(diff).forEach(([key, v]) => {
    const tmp = key as CardId
    if (stateValue && stateValue[tmp]) {
      stateValue[tmp]!.clans1 += v.clans1
      stateValue[tmp]!.clans2 += v.clans2
      stateValue[tmp]!.sanctuaries = v.sanctuaries
      stateValue[tmp]!.citadels += v.citadels
      stateValue[tmp]!.festival = v.festival
      if (v.capital !== stateValue[tmp]!.capital)
        console.error('FIXME: Capital is changed')
      stateValue[tmp]!.capital = v.capital
    } else {
      stateValue[tmp] = { ...v }
    }
  })
  return stateValue
}

export const emptyLocationDataDiffForStateValue = (locations: {
  [id in CardId]?: LocationData
}) => {
  let tmp = {} as LocationDataDiffs
  Object.entries(locations).forEach(([key, v]) => {
    const k = key as CardId
    tmp[k] = {
      ...emptyLocationDataDiff,
      cardId: v.cardId,
    }
  })
  console.log(tmp)
  return tmp
}

export const copyLocationDataDiffs = (
  diff: Partial<LocationDataDiffs> | undefined
): LocationDataDiffs | undefined => {
  if (!diff) return undefined
  let retValue: LocationDataDiffs = {}
  Object.entries(diff).forEach(([k, v]) => {
    console.log('checking', k, v)
    if (!!v) {
      const vv = v as unknown as LocationDataDiff
      retValue[k as unknown as CardId] = { ...vv }
    }
  })
  console.log('copy from', JSON.stringify(diff), 'to', JSON.stringify(retValue))
  return retValue
}
