import { globals } from '../../game-data/globals'
import { CardId } from '../../game-data/types'
import {
  LocationData,
  LocationDataDiff,
  LocationDataDiffs,
  PlayerId,
  useGameStore,
} from '../../stores/useGameStore'
import { useInfoStore } from '../../stores/useInfoStore'
import { useScenesStore } from '../../stores/useScenesStore'
import {
  drawLocationCard,
  emptyLocationDataDiffForStateValue,
} from '../../utils/utils'
import {
  StateManagerInterface,
  PhaseData,
  State,
  StateData,
  GameEvent,
  stateLog,
} from '../useStateManager'

var stateValue: LocationDataDiffs = {}

type StatesPhase = Extract<
  State,
  | 'stInit'
  | 'stStartSelectingPlayer'
  | 'stFinishingSelectingPlayer'
  | 'stLocationInstalling'
  | 'stSettingUpInitialClans'
  | 'stSettingUpInitialClansWait'
  | 'stSettingUpInitialClansDone'
>

export const usePhaseSetup = (phaseAndState: StateManagerInterface) => {
  const {
    updatePhase,
    updateState,
    getState,
    getPhase,
    sendLocal,
    sendRemote,
    received,
    setupNetwork,
    updatePeerParams,
  } = phaseAndState
  const { setScene } = useScenesStore()
  const {
    activePlayerOfTurn: activePlayer,
    setActivePlayerOfTurn: setActivePlayer,
    addLocation,
    updateLocation,
    setPlayerData,
    locations,
  } = useGameStore()
  const { setActiveInfo: setActiveInfoInternal } = useInfoStore()

  const setActiveInfo = (desc: string) => {
    setActiveInfoInternal({
      desc,
      player: activePlayer,
      phase: getPhase(),
      state: getState(),
    })
  }

  const tempInstallLocations = () => {
    let locationTemplate: LocationData = {
      cardId: 'aBard', // tmp
      clans1: 0,
      clans2: 0,
      sanctuaries: 0,
      citadels: 0,
      festival: false,
      leader: 0,
      capital: false,
    }
    addLocation({
      ...locationTemplate,
      cardId: drawLocationCard(),
      capital: true,
    })
    addLocation({
      ...locationTemplate,
      cardId: drawLocationCard(),
    })
    sendLocal('evLocationInstalled', null)
  }

  const phase: PhaseData = {
    id: 'pSetup',
    states: [
      'stInit',
      'stStartSelectingPlayer',
      'stFinishingSelectingPlayer',
      'stLocationInstalling',
      'stSettingUpInitialClans',
      'stSettingUpInitialClansWait',
      'stSettingUpInitialClansDone',
    ],
    goNext: (value) => {
      const from = getState()
      switch (from) {
        case 'stInit':
          updateState('stStartSelectingPlayer', value)
          break
        case 'stStartSelectingPlayer':
          updateState('stFinishingSelectingPlayer', value)
          break
        case 'stFinishingSelectingPlayer':
          if (value) updateState('stLocationInstalling', value)
          else updateState('stFinishingSelectingPlayer', value)
          break
        case 'stLocationInstalling':
          stateValue = emptyLocationDataDiffForStateValue(locations)
          if (globals.currentPlayer === activePlayer) {
            updateState('stSettingUpInitialClans', value)
          } else updateState('stSettingUpInitialClansWait', value)
          break
        case 'stSettingUpInitialClansWait':
        case 'stSettingUpInitialClans':
          stateValue = value as LocationDataDiffs
          stateLog('stSettingUpInitialClans')
          let clans1 = 0
          let clans2 = 0
          if (stateValue)
            Object.entries(stateValue).forEach(([k, v]) => {
              clans1 += v.clans1
              clans2 += v.clans2
            })
          if (stateValue && clans1 === 2 && clans2 === 2) {
            setScene('scModifyClansData', false, {})
            updateState('stSettingUpInitialClansDone', value)
          } else {
            if (from === 'stSettingUpInitialClansWait')
              updateState('stSettingUpInitialClans', value)
            else updateState('stSettingUpInitialClansWait', value)
          }
          break
        case 'stSettingUpInitialClansDone':
          stateValue = {}
          updatePhase('pDraftCards', value)
          break
        default:
          break
      }
    },
  }

  const states: { [id in StatesPhase]: StateData } = {
    stInit: {
      id: 'stInit',
      validEvents: ['evStart'],
      handle: function (event: GameEvent, value: any): any {},
    },
    stStartSelectingPlayer: {
      id: 'stStartSelectingPlayer',
      initialize: function (): void {
        const playerId = window.location.search.length === 0 ? 1 : 2
        setActiveInfo(`You are player${playerId}`)
        globals.currentPlayer = playerId as PlayerId
        if (playerId === 1) {
          // player 1
          setupNetwork(playerId as PlayerId, '')
        } else {
          // player2
          setupNetwork(playerId as PlayerId, window.location.search)
        }
        setScene('scSelectPlayer', true, undefined)
      },
      validEvents: ['evPlayer1UrlGenerated', 'evPlayer2PeerParamGenerated'],
      handle: function (event: GameEvent, value: any): any {
        if (event === 'evPlayer1UrlGenerated') {
          setScene('scSelectPlayer', true, { player1Url: value })
        } else {
          setScene('scSelectPlayer', true, { player2PeerParam: value })
        }
        // const { playerId, peerParams } = value
        // globals.currentPlayer = playerId as PlayerId
        // setupNetwork(playerId as PlayerId, peerParams)
        setActivePlayer(1)
        // setScene('scSelectPlayer', false, undefined)
      },
    },
    stFinishingSelectingPlayer: {
      id: 'stFinishingSelectingPlayer',
      initialize: function (): void {},
      validEvents: ['evPeerParamsUpdatedForPlayer1', 'evPeerConnected'],
      handle: function (event: GameEvent, value: any): any {
        if (event === 'evPeerParamsUpdatedForPlayer1') {
          updatePeerParams(value)
          return false
        } else if (event === 'evPeerConnected') {
          setScene('scSelectPlayer', false, undefined)
          return true
        }
      },
    },
    stLocationInstalling: {
      id: 'stLocationInstalling',
      initialize: () => {
        tempInstallLocations()
      },
      validEvents: ['evLocationInstalled'],
      handle: function (event: GameEvent, value: any): any {},
    },
    stSettingUpInitialClansWait: {
      id: 'stSettingUpInitialClansWait',
      initialize: () => {
        setScene('scWaitForOpponent', true, stateValue)
        setScene('scModifyClansData', true, stateValue)
        setActiveInfo('Waiting for opponent...')
        // dummy
        received('evLocationUpdated', {})
      },
      validEvents: ['evLocationUpdated'],
      handle: function (event: GameEvent, value: any): any {
        setActiveInfo('')
        setScene('scWaitForOpponent', false, stateValue)
      },
    },
    stSettingUpInitialClans: {
      id: 'stSettingUpInitialClans',
      initialize: () => {
        setScene('scModifyClansControl', true, null)
        setScene('scModifyClansData', true, stateValue)
      },
      validEvents: ['evLocationUpdated'],
      handle: function (event: GameEvent, value: any): any {
        setScene('scModifyClansControl', false, null)
        // DEV
        // return value
        return {
          lForest: {
            clans1: 2,
            clans2: 0,
            sanctuaries: 0,
            citadels: 0,
            festival: false,
            capital: false,
            cardId: 'lForest',
          },
          lCove: {
            clans1: 0,
            clans2: 2,
            sanctuaries: 0,
            citadels: 0,
            festival: false,
            capital: false,
            cardId: 'lCove',
          },
        }
      },
    },
    stSettingUpInitialClansDone: {
      id: 'stSettingUpInitialClansDone',
      initialize: () => {
        const newValue = stateValue as { [id in CardId]: LocationDataDiff }
        console.log(JSON.stringify(newValue))
        Object.keys(newValue).forEach((key) => {
          updateLocation(newValue[key as CardId])
        })
        sendLocal('evGoHandle', null)
      },
      validEvents: ['evGoHandle'],
      handle: function (event: GameEvent, value: any): any {},
    },
  }

  return { phase, states }
}
