import { globals } from '../../game-data/globals'
import { CardId } from '../../game-data/types'
import { PlayerId, useGameStore } from '../../stores/useGameStore'
import { useScenesStore } from '../../stores/useScenesStore'
import { drawActionCard } from '../../utils/utils'
import {
  GameEvent,
  PhaseData,
  State,
  StateData,
  stateLog,
  StateManagerInterface,
} from '../useStateManager'

export type DraftCardState = {
  cards: { [cardId in CardId]?: PlayerId }
  tempCards: CardId[]
  tempPlayer: PlayerId
  selectingCount: number
  1: CardId[]
  2: CardId[]
}

export type DraftCardStateResult = {
  player: PlayerId
  cards: CardId[]
}

const stateValueEmpty: DraftCardState = {
  cards: {},
  tempCards: [],
  tempPlayer: 1,
  selectingCount: 2,
  1: [],
  2: [],
}

var stateValue: DraftCardState = { ...stateValueEmpty, cards: {} }
type StatesPhase = Extract<
  State,
  'stDraftCardsSetup' | 'stDistributeCards' | 'stDraftCardsDone'
>

const dev1Config = false // send remote selecting
const dev2Config = true // predefined selecting
var dev1 = false

export const usePhaseDraftCards = (phaseAndState: StateManagerInterface) => {
  const { updateState, updatePhase, getState, sendLocal, received } =
    phaseAndState
  const { setScene } = useScenesStore()
  const { setPlayerData } = useGameStore()

  const phase: PhaseData = {
    id: 'pDraftCards',
    states: ['stDraftCardsSetup', 'stDistributeCards', 'stDraftCardsDone'],
    goNext: (value: any) => {
      const from = getState()
      switch (from) {
        case 'stDraftCardsSetup':
          updateState('stDistributeCards', null)
          break
        case 'stDistributeCards':
          if (value) {
            updateState('stDraftCardsDone', null)
          } else {
            updateState('stDistributeCards', null)
          }
          break
        case 'stDraftCardsDone':
          if (!value) {
            updateState('stDraftCardsSetup', null)
          } else {
            stateValue = stateValueEmpty
            updatePhase('pSeason', null)
          }
          break
        default:
          break
      }
    },
  }

  const states: { [id in StatesPhase]: StateData } = {
    stDraftCardsSetup: {
      id: 'stDraftCardsSetup',
      initialize: () => {
        dev1 = false
        stateValue.cards[drawActionCard()] = 1
        stateValue.cards[drawActionCard()] = 1
        stateValue.cards[drawActionCard()] = 1
        stateValue.cards[drawActionCard()] = 2
        stateValue.cards[drawActionCard()] = 2
        stateValue.cards[drawActionCard()] = 2
        stateValue.selectingCount = 2
        stateLog()
        sendLocal('evGoHandle', null)
      },
      validEvents: ['evGoHandle'],
      handle: function (): any {},
    },
    stDistributeCards: {
      id: 'stDistributeCards',
      initialize: () => {
        setScene('scDraftCards', true, {
          ...stateValue,
          cards: { ...stateValue.cards },
          tempCards: [...stateValue.tempCards],
          1: [...stateValue[1]],
          2: [...stateValue[2]],
        })
        // dummy [[
        if (dev1Config) {
          if (!dev1) {
            let cards: CardId[] = []
            let count: number = stateValue.selectingCount
            Object.entries(stateValue.cards).forEach(([k, v]) => {
              if (k !== 'confirmed' && k !== 'selectingCount' && k !== 'temp') {
                if (count > 0 && v === 2) {
                  cards.push(k as CardId)
                  count -= 1
                }
              }
            })
            received('evDraftCardsSelected', { player: 2, cards })
            stateValue = {
              ...stateValue,
              tempCards: stateValue.tempCards ? [...stateValue.tempCards] : [],
            }

            dev1 = true
          }
        }
        if (dev2Config) {
          sendLocal('evDraftCardsSelected', null)
        }
        // ]] dummy
      },
      validEvents: ['evDraftCardsSelected'], // selected to give
      handle: function (event: GameEvent, value: any): any {
        if (dev2Config) {
          setScene('scDraftCards', false, null)
          stateValue = {
            '1': ['aGeits', 'aDruid', 'aMigration'],
            '2': ['aNewAliance', 'aNewClans', 'aExploration'],
            cards: {
              aCitadel: 1,
              aBard: 1,
              aConquest: 2,
              aFestival: 1,
              aWarLord: 2,
              aSanctuary: 2,
            },
            tempCards: [],
            tempPlayer: 2,
            selectingCount: 1,
          }

          return true
        } else {
          setScene('scDraftCards', false, null)
          const val = value as DraftCardStateResult

          if (stateValue.tempCards.length > 0) {
            const tmp = [...stateValue.tempCards]
            val.cards.forEach((cardId) => {
              stateValue.cards = {
                ...stateValue.cards,
                [cardId]: stateValue.tempPlayer,
              }
            })
            tmp.forEach((cardId) => {
              stateValue.cards = {
                ...stateValue.cards,
                [cardId]: val.player,
              }
            })
            stateValue.tempCards = []
            console.warn(
              'stateValue:',
              JSON.stringify(stateValue),
              stateValue.selectingCount
            )
            if (stateValue.selectingCount === 2) {
              stateValue.selectingCount = 1
              setScene('scDraftCardsWait', false, null)
              dev1 = false
              return false
            }
            return true
          }
          stateValue.tempPlayer = val.player
          stateValue.tempCards = [...val.cards]
          if (val.player === globals.currentPlayer)
            setScene('scDraftCardsWait', true, null)

          return false
        }
      },
    },
    stDraftCardsDone: {
      id: 'stDraftCardsDone',
      initialize: () => {
        if (stateValue[1].length === 0) {
          // first visit
          const tmp = stateValue.cards
          Object.entries(tmp).forEach(([k, v]) => {
            stateValue[v as PlayerId].push(k as CardId)
          })
          stateValue.cards = {}
          sendLocal('evGoHandle', false)
        } else {
          Object.entries(stateValue.cards).forEach(([k, v]) => {
            if (v === 1) stateValue[1].push(k as CardId)
            else stateValue[2].push(k as CardId)
          })
          setPlayerData(1, {
            cardIds: [...stateValue[1]],
          })
          setPlayerData(2, {
            cardIds: [...stateValue[2]],
          })
          sendLocal('evGoHandle', true)
        }
      },
      validEvents: ['evGoHandle'],
      handle: function (event: GameEvent, value: any): any {
        return value
      },
    },
  }

  return { phase, states }
}
