import { useState } from 'react'
import { CardId } from '../game-data/types'
import { allCards } from '../game-data/cards'
import { Card, CardBody, CardFooter, CardHeader, Text } from '@chakra-ui/react'

export type GameCardProps = {
  cardId: CardId
  width: number
  height: number
  onClick?: (cardId: CardId, isActive: boolean) => void
}

export const GameCard = (props: GameCardProps) => {
  const [isActive, setIsActive] = useState(false)
  const { cardId, width, height, onClick } = props
  const handleClick = () => {
    if (onClick) {
      onClick(cardId, !isActive)
      setIsActive(!isActive)
    }
  }

  const info = allCards[cardId]

  return (
    <Card
      w={`${width}dvw`}
      h={`${height}dvh`}
      bg={
        isActive
          ? 'gray'
          : info.type === 'action'
          ? 'green'
          : info.type === 'epic'
          ? 'red'
          : 'yellow'
      }
      overflow='hidden'
      onClick={handleClick}
    >
      <CardHeader h='4dvh' p='5px'>
        {info.img}
        {'image'}
      </CardHeader>
      <CardBody h='7vh' p='0px' bg='#FFFFFF' overflow='hidden'>
        {info.title}({info.id})
      </CardBody>
      <CardFooter></CardFooter>
    </Card>
  )
}
