import { create } from 'zustand'
import { devtools } from 'zustand/middleware'
import { immer } from 'zustand/middleware/immer'
import { Card, CardId } from '../game-data/types'

export type LocationData = {
  cardId: CardId
  positionX?: number
  positionY?: number
  clans1: number
  clans2: number
  sanctuaries: number
  citadels: number
  festival: boolean
  leader: PlayerId | 0
  capital: boolean
}

export type LocationDataDiff = Omit<
  LocationData,
  'positionX' | 'positionY' | 'leader'
>

export const emptyLocationDataDiff: LocationDataDiff = {
  clans1: 0,
  clans2: 0,
  sanctuaries: 0,
  citadels: 0,
  festival: false,
  capital: false,
  cardId: 'aMigration',
}

export type LocationDataDiffs = Partial<{
  [id in CardId]: LocationDataDiff
}>

export type GameData = {
  activePlayerOfTurn: PlayerId
  cardsInActive: Card
  cardsPlayed: Card[]
  epicCards: Card[]
  idleLandCards: Card[]
}

export type PlayerData = {
  idleClans: number
  cardIds: CardId[]
  deedEnd1: boolean
  deedEnd2: boolean
}

export type PlayerDataDiff = Partial<PlayerData>

export type PlayerId = 1 | 2

type GameStore = {
  locations: { [id in CardId]?: LocationData }
  addLocation: (location: LocationData) => void
  updateLocation: (diff: LocationDataDiff) => void
  activePlayerOfTurn: PlayerId
  brenn: PlayerId
  playerData: { [id in PlayerId]: PlayerData }
  setPlayerData: (player: 1 | 2, data: PlayerDataDiff) => void
  setActivePlayerOfTurn: (player: PlayerId) => void
}

export const useGameStore = create<GameStore>()(
  devtools(
    immer((set, get) => ({
      activePlayerOfTurn: 1,
      brenn: 1,
      playerData: {
        1: {
          idleClans: 15,
          cardIds: [],
          deedEnd1: false,
          deedEnd2: false,
        },
        2: {
          idleClans: 15,
          cardIds: [],
          deedEnd1: false,
          deedEnd2: false,
        },
      },
      locations: {},
      addLocation: (location: LocationData) =>
        set((state) => {
          console.log('addLocation:', location)
          state.locations[location.cardId!] = location
        }),
      updateLocation: (diff: LocationDataDiff) =>
        set((state) => {
          state.playerData['1'].idleClans -= diff.clans1
          state.playerData['2'].idleClans -= diff.clans2
          state.locations[diff.cardId]!.clans1 += diff.clans1
          state.locations[diff.cardId]!.clans2 += diff.clans2
          state.locations[diff.cardId]!.sanctuaries = diff.sanctuaries
          state.locations[diff.cardId]!.citadels += diff.citadels
          state.locations[diff.cardId]!.festival = diff.festival
          if (diff.capital !== state.locations[diff.cardId]!.capital)
            console.error('FIXME: Capital is changed')
          state.locations[diff.cardId]!.capital = diff.capital
          if (
            state.locations[diff.cardId]!.clans1 !==
            state.locations[diff.cardId]!.clans2
          ) {
            state.brenn =
              state.locations[diff.cardId]!.clans1 >
              state.locations[diff.cardId]!.clans2
                ? 1
                : 2
          }
        }),

      setPlayerData: (player: PlayerId, data: PlayerDataDiff) =>
        set((state) => {
          const { idleClans, cardIds, deedEnd1, deedEnd2 } = data
          if (idleClans) state.playerData[player].idleClans = idleClans
          if (cardIds) state.playerData[player].cardIds = cardIds
          if (deedEnd1) state.playerData[player].deedEnd1 = deedEnd1
          if (deedEnd2) state.playerData[player].deedEnd2 = deedEnd2
        }),
      setActivePlayerOfTurn: (player: PlayerId) =>
        set((state) => {
          state.activePlayerOfTurn = player
        }),
    }))
  )
)
