import { Box, Text } from '@chakra-ui/react'
import { useInfoStore } from '../stores/useInfoStore'
import { useScenesStore } from '../stores/useScenesStore'
import { CardId } from '../game-data/types'
import { GameCard } from './GameCard'
import { PlayerId } from '../stores/useGameStore'

export const InfoBox = () => {
  const { info } = useInfoStore()
  const { scTurnInfo } = useScenesStore()
  const { desc, player } = info
  const cards = scTurnInfo.data ? scTurnInfo.data.cards : []
  return (
    <Box>
      {player} Player: {desc}
      {scTurnInfo.rendered
        ? cards.map((c: { player: PlayerId; cardId: CardId }) => (
            <GameCard
              key={c.cardId}
              cardId={c.cardId}
              width={10}
              height={10}
              onClick={function (cardId: CardId, isActive: boolean): void {}}
            />
          ))
        : undefined}
    </Box>
  )
}
