import {
  Box,
  Button,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  HStack,
  Text,
} from '@chakra-ui/react'
import { allCards } from '../game-data/cards'
import { useScenesStore } from '../stores/useScenesStore'
import { useStateManager } from '../hooks/useStateManager'
import { CardId } from '../game-data/types'
import { globals } from '../game-data/globals'
import { MutableRefObject, useRef, useState } from 'react'
import { useInfoStore } from '../stores/useInfoStore'
import {
  DraftCardState,
  DraftCardStateResult,
} from '../hooks/game-phases/usePhaseDraftCards'
import { GameCard, GameCardProps } from './GameCard'

export const DraftCards = () => {
  const { scDraftCards, scDraftCardsWait } = useScenesStore()
  const { rendered: mainRendered, data } = scDraftCards
  const { rendered: waitRendered } = scDraftCardsWait
  const { sendLocal } = useStateManager()
  const { setActiveInfo: setActiveInfoInternal } = useInfoStore()
  const selectedRef = useRef([]) as MutableRefObject<CardId[]>

  let { selectingCount, cards, tempCards } = data as DraftCardState
  const cardIds: CardId[] = []
  const cardIdsOpp: CardId[] = []
  const cardIdsTempSelected = tempCards

  Object.entries(cards).forEach(([k, v]) => {
    if (v === globals.currentPlayer) cardIds.push(k as CardId)
    else cardIdsOpp.push(k as CardId)
  })

  const setActiveInfo = (desc: string) => {
    setActiveInfoInternal({
      desc,
      player: globals.currentPlayer,
      phase: 'pDraftCards',
      state: 'stDistributeCards',
    })
  }

  const handleConfirm = () => {
    if (selectedRef.current.length !== selectingCount) {
      setActiveInfo('Invalid Selection')
    } else {
      sendLocal('evDraftCardsSelected', {
        player: globals.currentPlayer,
        cards: selectedRef.current,
      } as DraftCardStateResult)
    }
  }

  const handleClick = (cardId: CardId, isActive: boolean) => {
    const index = selectedRef.current.indexOf(cardId)
    if (isActive)
      if (index === -1) selectedRef.current.push(cardId)
      else console.log('error', cardId, selectedRef.current)
    else {
      if (index > -1) selectedRef.current.splice(index, 1)
      else console.log('error', cardId, selectedRef.current)
    }
  }

  const cardProps: Omit<GameCardProps, 'cardId'> = {
    width: 10,
    height: 20,
    onClick: handleClick,
  }
  return (
    <>
      {waitRendered ? null : (
        <HStack>
          {cardIds.map((cardId) => (
            <GameCard key={cardId} {...cardProps} cardId={cardId} />
          ))}
          <Button onClick={handleConfirm}>Confirm</Button>
        </HStack>
      )}

      <HStack>
        {cardIdsOpp.map((cardId) => (
          <GameCard key={cardId} {...cardProps} cardId={cardId} />
        ))}
      </HStack>

      <HStack>
        {cardIdsTempSelected.map((cardId) => (
          <GameCard key={cardId} {...cardProps} cardId={cardId} />
        ))}
      </HStack>
    </>
  )
}
