import { create } from 'zustand'
import { devtools } from 'zustand/middleware'
import { immer } from 'zustand/middleware/immer'
import { PlayerId } from './useGameStore'
import { Phase, State } from '../hooks/useStateManager'

export type Info = {
  desc: string
  player: 0 | PlayerId
  phase: Phase
  state: State
  event?: Event
}

type InfoStore = {
  info: Info
  setActiveInfo: (info: Info) => void
}

export const useInfoStore = create<InfoStore>()(
  devtools(
    immer((set, get) => ({
      info: {
        desc: '',
        player: 0,
        phase: 'pSetup',
        state: 'stInit',
      },
      setActiveInfo: (info: Info) => {
        set((state) => {
          state.info = info
        })
      },
    }))
  )
)
