import { Box, Button } from '@chakra-ui/react'
import { Map } from './components/Map'
import { ChoosePlayer } from './components/ChoosePlayer'
import { useEffect } from 'react'
import { useScenesStore } from './stores/useScenesStore'
import { InfoBox } from './components/InfoBox'
import { useStateManager } from './hooks/useStateManager'
import { DraftCards } from './components/DraftCard'
import { CardDrawerBox } from './components/CardDrawer/CardDrawerBox'

export const Main = () => {
  const { scSelectPlayer, scDraftCards, scCardSelecting } = useScenesStore()
  const { sendLocal } = useStateManager()

  useEffect(() => {
    sendLocal('evStart', null)
  }, [])

  return (
    <Box
      id='main'
      width='100dvw'
      height='100dvh'
      position='absolute'
      overflow='hidden'
    >
      {/* {phase === 'pDraftCards' ? <DraftCards /> : undefined} */}
      <InfoBox />

      {scSelectPlayer.rendered ? (
        <ChoosePlayer
          player1Url={scSelectPlayer.data?.player1Url}
          player2PeerParam={scSelectPlayer.data?.player2PeerParam}
        />
      ) : undefined}

      {scDraftCards.rendered ? <DraftCards /> : undefined}

      {scCardSelecting.rendered ? <CardDrawerBox></CardDrawerBox> : undefined}

      <Map></Map>
    </Box>
  )
}
