import { MouseEventHandler, MutableRefObject } from 'react'
import { useGameStore } from '../../stores/useGameStore'
import { allCards } from '../../game-data/cards'
import {
  Accordion,
  AccordionButton,
  AccordionItem,
  AccordionPanel,
  Button,
  Drawer,
  DrawerBody,
  DrawerCloseButton,
  DrawerContent,
  DrawerHeader,
  DrawerOverlay,
} from '@chakra-ui/react'
import { globals } from '../../game-data/globals'
import { Card, CardId } from '../../game-data/types'
import { useScenesStore } from '../../stores/useScenesStore'
import { useStateManager } from '../../hooks/useStateManager'
import { CardSelectingResult } from '../../hooks/game-phases/usePhaseSeason'

export const CardDrawerButton = () => {}

export const CardDrawer = (props: {
  onClose: () => void
  isOpen: boolean
  btnRef: MutableRefObject<HTMLButtonElement>
}) => {
  const { isOpen, btnRef, onClose } = props
  const { playerData: players, activePlayerOfTurn } = useGameStore()
  const { scCardSelecting } = useScenesStore()
  const { data } = scCardSelecting
  const { cardPlayType, showConfirm, hasGeis } = data
  const { sendLocal } = useStateManager()

  const { cardIds } = players[activePlayerOfTurn]

  let cards: Card[] = []
  if (cardIds && cardIds.length > 0)
    cards = cardIds.map((id: CardId) => allCards[id])

  const handleClick: MouseEventHandler<HTMLButtonElement> = (evt) => {
    if (showConfirm) {
      let cardId = (evt.target as HTMLButtonElement).value as
        | CardId
        | ''
        | undefined
      if (cardId === '') cardId = undefined
      const result: CardSelectingResult = {
        player: globals.currentPlayer,
        cardId,
      }
      sendLocal('evCardSelected', result)
      onClose()
    } else {
      console.log('no confirm is allowed')
    }
  }
  return (
    <Drawer
      isOpen={isOpen}
      placement='left'
      onClose={onClose}
      finalFocusRef={btnRef}
    >
      <DrawerOverlay />
      <DrawerContent>
        <DrawerCloseButton />
        <DrawerHeader>Cards ${cardPlayType} </DrawerHeader>
        <DrawerBody>
          <Accordion>
            {cardPlayType === 'triskel' ? (
              <Button w='100%' value={undefined} onClick={handleClick}>
                Not Play Card
              </Button>
            ) : undefined}
            {cards.map((card) => (
              <AccordionItem key={card.id}>
                <Button w='100%' value={card.id} onClick={handleClick}>
                  {card.title}
                </Button>
                <AccordionButton
                  bg={card.type === 'action' ? '#44FF44' : '#FF4444'}
                >
                  {card.desc}
                </AccordionButton>
                <AccordionPanel>{card.desc}</AccordionPanel>
              </AccordionItem>
            ))}
          </Accordion>
        </DrawerBody>

        {/* <DrawerFooter>
          <Button variant='outline' mr={3} onClick={onClose}>
            Cancel
          </Button>
          <Button colorScheme='blue'>Save</Button>
        </DrawerFooter> */}
      </DrawerContent>
    </Drawer>
  )
}
